/* 노말라이징 */
body,
ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

/* 라이브러리 */
.con {
  max-width: 1000px;
  margin: 0 auto;
}

/* 커스텀 */
@import url(//fonts.googleapis.com/earlyaccess/notosanskr.css);

body {
  font-family: "Noto Sans KR", sans-serif;
  overflow-y: hidden;
}

body,
html {
  height: 100%;
}

#chat-room {
  height: 100%;
  background-color: #9bbbd4;
  /* 임시 */
  outline: 2px solid black;
  position: relative;
}

#chat-room .message-box {
  padding: 0 0.7rem;
  overflow-y: scroll;
  height: 720px;
  width: 1550px;
}

#chat-room .message-group::before {
  content: attr(data-date-str);
  display: block;
  background-color: rgb(0, 0, 0, 0.15);
  border-radius: 1rem;
  text-align: center;
  padding: 0.3rem 0;
  color: white;
  font-size: 1.6rem;
}

#chat-room .chat-message {
  margin-left: 3rem;
  position: relative;
}

#chat-room .chat-message > section {
  position: absolute;
  top: 0;
  left: -3rem;
}

#chat-room .chat-message > button {
  display: block;
}

#chat-room .chat-message > section {
  font-size: 3rem;
}

#chat-room .chat-message::after {
  content: "";
  display: block;
  clear: both;
}

#chat-room .chat-message > div {
  background-color: white;
  float: left;
  padding: 0.8rem;
  border-radius: 1rem;
  clear: both;
  font-weight: bold;
  font-size: 1.46rem;
  box-shadow: 1px 1px 0 rgba(0, 0, 0, 0.3);
}

#chat-room .chat-message.mine > div {
  background-color: #fdf01b;
  float: right;
  box-shadow: -1px 1px 0 rgba(0, 0, 0, 0.3);
}

#chat-room .chat-message.mine > span {
  display: none;
}

#chat-room .chat-message.mine > section {
  display: none;
}

#chat-room .input-box {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  border: 1px solid transparent;
}

#chat-room .input-box #text-input {
  width: 100%;
  display: block;
  border: 0;
  outline: none;
  padding-right: 8.5rem;
  padding-left: 4.8rem;
  padding-top: 0.1rem;
  font-size: 1.4rem;
  line-height: 4rem;
  font-weight: bold;
  box-sizing: border-box;
}

#chat-room .input-box .btn-plus {
  position: absolute;
  top: 0;
  left: 0;
  width: 4rem;
  height: 100%;
  background-color: #d1d1d1;
}

#chat-room .input-box .btn-plus > i {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-45%);
  color: white;
  font-size: 3rem;
}

#chat-room .input-box .btn-submit {
  position: absolute;
  right: 1rem;
  top: 50%;
  transform: translateY(-50%);
  padding: 10px;
  background-color: #fdf01b;
  padding: 10px;
  color: #bfb73d;
  border-radius: 3px;
  font-size: 1.3rem;
  box-shadow: 0 1px 10px rgba(0, 0, 0, 0.2);
  user-select: none;
  border: 1px solid transparent;
}

#chat-room .input-box .btn-submit:active {
  box-shadow: inset -0.3rem -0.1rem 1.4rem #fbfbfb,
    inset 0.3rem 0.4rem 0.8rem #bec5d0;
  cursor: pointer;
}

#chat-room .input-box .btn-emo {
  position: absolute;
  right: 6rem;
  top: 50%;
  transform: translateY(-50%);
  font-size: 2rem;
  color: #cbcbcb;
  user-select: none;
}
